.form__fieldset {
  border: 3px solid #5e6161;
  padding: 20px 0;
  border-radius: 5px;
}

.form__legend {
  border: 2px solid #5e6161;
  border-radius: 5px;
  padding: 10px 10px;
  border-radius: 5px;
  background-color: #ec8422;
  color: white;
}

.form__labelText {
  display: inline-block;
  padding: 0px 20px;
  max-width: 200px;
  width: 100%;
  color: black;
}

.form__field {
  display: inline-block;
  border: 2px solid #f07e14;
  max-width: 400px;
  width: 100%;
  padding: 10px;
  border-radius: 5px;
}

.form__button {
  border: 2px solid #5e6161;
  border-radius: 5px;
  background-color: #b1b4b6;
  padding: 20px 0;
  width: 100%;
}

.form__button:hover {
  background-color: #e28f41;
}

.form__button:active {
  background-color: #f7ad68;
}

.paragraph {
  padding-bottom: 20px;
}

@media (max-width: 650px) {
  .form__field {
    margin: 5px 0;
  }

  .form__labelText {
    padding: 2px;
  }

  .paragraph {
    text-align: center;
  }

  .form__paragraph {
   
    text-align: center;
  }

}
